.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 3px solid #2d2424;
  border-radius: 50%;
  margin: 20px auto;
  animation: animloader 2s linear infinite;
  display: block;
  position: relative;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 6px;
  height: 24px;
  background: #2d2424;
  position: absolute;
  bottom: -20px;
  left: 46px;
  transform: rotate(-45deg);
}

@keyframes animloader {
  0% {
    transform: translate(-10px, -10px);
  }

  25% {
    transform: translate(-10px, 10px);
  }

  50% {
    transform: translate(10px, 10px);
  }

  75% {
    transform: translate(10px, -10px);
  }

  100% {
    transform: translate(-10px, -10px);
  }
}

